	//监听页面返回 
	onBackPress() {
		if(this.isShow){
			this.hidePop();
			return true; 
		}
	},
	methods: {
		showPop() {
			var animation = uni.createAnimation({
				duration: 200
			})
			animation.translateY(600).step();
			this.animationData = animation.export();
			this.isShow = true;
			setTimeout(() => {
				animation.translateY(0).step();
				this.animationData = animation.export();
			}, 200)
		},
		hidePop() {
			var animation = uni.createAnimation({
				duration: 200
			})
			animation.translateY(600).step();
			this.animationData = animation.export();
			this.isShow = true;
			setTimeout(() => {
				animation.translateY(0).step();
				this.isShow = false;
			}, 200)

		}
	}
}
